import React from "react";

const PrimaryButton = ({ className, children, ...props }) => {
  return (
    <button
      className={`hover:bg-primary text-white py-4 px-4 rounded focus:outline-none focus:shadow-outline uppercase ${
        className ? className : ""
      }`}
      {...props}
    >
      {children}
    </button>
  );
};

export default PrimaryButton;
