import React from "react";
import loadable from "@loadable/component";

const WarningIcon = loadable(() => import("@material-ui/icons/Warning"));
const ErrorIcon = loadable(() => import("@material-ui/icons/Error"));
const CheckCircleIcon = loadable(() =>
  import("@material-ui/icons/CheckCircle")
);
const InfoIcon = loadable(() => import("@material-ui/icons/Info"));

// import WarningIcon from '@material-ui/icons/Warning';
// import ErrorIcon from '@material-ui/icons/Error';
// import CheckCircleIcon from '@material-ui/icons/CheckCircle';
// import InfoIcon from '@material-ui/icons/Info';

const Alert = ({ type, heading, description }) => {
  let alertTypeClass = "blue";
  let alertIcon = <InfoIcon className="text-blue-500" />;

  switch (type) {
    case "success":
      alertTypeClass = "green";
      alertIcon = <CheckCircleIcon className="text-green-500" />;
      break;
    case "warning":
      alertTypeClass = "yellow";
      alertIcon = <WarningIcon className="text-yellow-500" />;
      break;
    case "info":
      alertTypeClass = "teal";
      alertIcon = <InfoIcon className="text-teal-500" />;
      break;
    case "error":
      alertTypeClass = "red";
      alertIcon = <ErrorIcon className="text-red-500" />;
      break;
    default:
      alertTypeClass = "blue";
      alertIcon = <InfoIcon className="text-blue-500" />;
  }

  return (
    <div
      className={`w-full bg-${alertTypeClass}-100 border-t-4 border-${alertTypeClass}-500 rounded-b text-${alertTypeClass}-900 px-4 py-3 shadow-md mb-6`}
      role="alert"
    >
      <div className="flex items-center">
        <div className="pr-2">{alertIcon}</div>
        <div>
          <p className="font-bold">{heading}</p>
          {!!description && <p className="text-sm">{description}</p>}
        </div>
      </div>
    </div>
  );
};

export default Alert;
