import React, { useState } from "react";
import { Link } from "react-router-dom";
import Loader from "react-loader-spinner";

import forgotPasswordService from "services/api/auth/forgotPassword.service";

import PrimaryButton from "elements/PrimaryButton";
import Alert from "elements/Alert";

const ForgotPassword = () => {
  const [email, setEmail] = useState("");
  const [error, setError] = useState(null);
  const [success, setSuccess] = useState(false);
  const [isLoading, setisLoading] = useState(false);

  const handleEmailChange = ({ target }) => {
    let { value } = target;

    setEmail(value);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      setisLoading(true);
      setSuccess(null);
      setError(null);

      // console.log("email = ", email);

      await forgotPasswordService(email);

      setSuccess({
        message: "We have sent you an email to reset your password",
      });
      setEmail("");
    } catch (err) {
      let error = "Something went wrong";

      if (err.response) {
        error = err.response.data;
      } else {
        error.message = err.message ? err.message : err;
      }

      setError(error);
    } finally {
      setisLoading(false);
    }
  };

  return (
    <div className="w-full h-screen flex flex-col justify-center items-center bg-gray-300">
      <img
        className="w-16"
        src="/assets/images/botsite-logo.svg"
        alt="Botsite Logo"
      />
      <h2 className="text-4xl text-gray-700">Forgot Password</h2>
      <form
        className="w-independent-form bg-gray-300 rounded px-4 py-4 pt-8"
        onSubmit={handleSubmit}
      >
        {!!error && <Alert type="error" heading={error.message} />}
        {!!success && <Alert type="success" heading={success.message} />}
        <div className="pb-4">
          <label htmlFor="email" className="text-sm block text-gray-500 pb-2">
            Email
          </label>
          <input
            type="email"
            name="email"
            id="email"
            className="shadow appearance-none border rounded w-full py-4 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline border-gray-300 "
            placeholder="Johnbull@example.com"
            value={email}
            onChange={handleEmailChange}
          />
        </div>
        <div>
          {isLoading ? (
            <PrimaryButton
              className="w-full bg-primary-1"
              type="button"
              disabled
            >
              <Loader
                className="flex items-center justify-center"
                type="ThreeDots"
                color="#FFFFFF"
                secondaryColor="#FFFFFF"
                height={25}
                width={40}
                timeout={0}
              />
            </PrimaryButton>
          ) : (
            <PrimaryButton className="w-full bg-primary-2" type="submit">
              Request New Password
            </PrimaryButton>
          )}
        </div>
      </form>
      <div>
        <p className="py-2 pt-6 text-gray-600">
          <Link to="/login" className="text-blue-400">
            Back To Login
          </Link>
        </p>
      </div>
    </div>
  );
};

export default ForgotPassword;
